/* Animations */
.nav-pills .nav-link,
.navbar,
.nav-tabs .nav-link,
.sidebar .nav a,
.sidebar .nav a i,
.sidebar .nav p,
.navbar-collapse .navbar-nav .nav-link,
.tag,
.tag [data-role="remove"],
.animation-transition-general {
  @include transitions($general-transition-time, $transition-ease);
}

.h-100vh {
  height: 100vh !important;
}
